*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html,
body{
  background-color: #1b1b32;
}

a
{
  text-decoration: none;
  color: white;
}
a:hover
{
  color: darkred;
}
.App
{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding-top: 15px;
  font-family: Lato, sans-serif;
}

.contenedorTitulo
{
  width: 100%;
  display: flex;
  font-size: 35px;
  align-items: center;
  justify-content: center;
  margin: 20px;
  color: aliceblue;
}

.contenedorPrincipal
{
  height: 550px;
  min-width: 600px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}